import React, { useState } from 'react';
import { useRouter } from 'next/router';

const AuthPage = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  
  const router = useRouter();
  
  // Mock auth functions (replace with actual implementations)
  const loginUser = async (username, password) => {
    // Simulate API call
    return new Promise(resolve => {
      setTimeout(() => resolve({ id: 1, username }), 1000);
    });
  };
  
  const registerUser = async (username, password, firstName, lastName) => {
    // Simulate API call
    return new Promise(resolve => {
      setTimeout(() => resolve({ id: 1, username, firstName, lastName }), 1000);
    });
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      let response;
      if (isLogin) {
        response = await loginUser(username, password);
      } else {
        response = await registerUser(username, password, firstName, lastName);
      }

      // Check if response is a successful profile object
      if (response && (response.id || response.username)) {
        router.push('/dashboard'); // Redirect on success
      } else {
        // If it's an error response
        setError(response.errorMessage || 'An error occurred');
      }
    } catch (error) {
      setError('An error occurred. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Left Side - Image or Signup Form */}
      <div className="w-1/2 bg-blue-600 flex flex-col justify-between">
        {isLogin ? (
          // Image section when login is active
          <div className="p-8 flex flex-col h-full">
            <div className="mb-8">
              <div className="h-8 w-8 bg-white rounded-md flex items-center justify-center">
                <span className="text-blue-600 font-bold">Q</span>
              </div>
            </div>
            <div className="my-8">
              <h2 className="text-white text-2xl font-bold mb-2">Designed for Individuals</h2>
              <p className="text-blue-100">See the analytics and grow your data remotely, from anywhere!</p>
            </div>
            <div className="flex-grow"></div>
            <div className="flex gap-1 mb-4">
              <div className="h-1 w-6 bg-white rounded-full"></div>
              <div className="h-1 w-1 bg-white/50 rounded-full"></div>
              <div className="h-1 w-1 bg-white/50 rounded-full"></div>
            </div>
          </div>
        ) : (
          // Signup form
          <div className="p-8 flex flex-col h-full">
            <div className="mb-8">
              <div className="h-8 w-8 bg-white rounded-md flex items-center justify-center">
                <span className="text-blue-600 font-bold">Q</span>
              </div>
            </div>
            <div className="my-8">
              <h2 className="text-white text-2xl font-bold">Create an Account</h2>
              <p className="text-blue-100 mt-2">Join our platform and start tracking your data</p>
            </div>
            
            <form onSubmit={handleSubmit} className="flex flex-col gap-4 my-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-white text-sm mb-1" htmlFor="firstName">First Name</label>
                  <input 
                    type="text"
                    id="firstName"
                    className="w-full p-2 rounded"
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                  />
                </div>
                <div>
                  <label className="block text-white text-sm mb-1" htmlFor="lastName">Last Name</label>
                  <input 
                    type="text"
                    id="lastName"
                    className="w-full p-2 rounded"
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                  />
                </div>
              </div>
              
              <div>
                <label className="block text-white text-sm mb-1" htmlFor="signup-username">Email Address</label>
                <input 
                  type="email"
                  id="signup-username"
                  className="w-full p-2 rounded"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
              </div>
              
              <div>
                <label className="block text-white text-sm mb-1" htmlFor="signup-password">Password</label>
                <input 
                  type="password"
                  id="signup-password"
                  className="w-full p-2 rounded"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              
              {error && <p className="text-red-200 text-sm">{error}</p>}
              
              <button 
                type="submit" 
                className="bg-white text-blue-600 py-2 rounded font-medium mt-2"
                disabled={loading}
              >
                {loading ? 'Creating Account...' : 'Sign Up'}
              </button>
            </form>
            
            <p className="text-blue-100 text-center mt-4">
              Already have an account?{' '}
              <button 
                className="text-white font-medium underline"
                onClick={() => setIsLogin(true)}
              >
                Log in
              </button>
            </p>
          </div>
        )}
      </div>
      
      {/* Right Side - Login or Image */}
      <div className="w-1/2 bg-white">
        {isLogin ? (
          // Login form
          <div className="p-8 flex flex-col justify-center h-full max-w-md mx-auto">
            <h2 className="text-2xl font-bold mb-8">Login</h2>
            
            <form onSubmit={handleSubmit} className="flex flex-col gap-4">
              <div>
                <label className="block text-gray-700 text-sm mb-1" htmlFor="login-username">Email address</label>
                <input 
                  type="email"
                  id="login-username"
                  className="w-full p-2 border rounded"
                  placeholder="name@mail.com"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
              </div>
              
              <div>
                <div className="flex justify-between mb-1">
                  <label className="block text-gray-700 text-sm" htmlFor="login-password">Password</label>
                  <a href="#" className="text-sm text-blue-600">Reset Password</a>
                </div>
                <input 
                  type="password"
                  id="login-password"
                  className="w-full p-2 border rounded"
                  placeholder="••••••••"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              
              <div className="flex items-center mt-2">
                <input 
                  type="checkbox" 
                  id="remember" 
                  className="h-4 w-4" 
                />
                <label htmlFor="remember" className="ml-2 text-sm text-gray-600">Remember Password</label>
              </div>
              
              {error && <p className="text-red-500 text-sm">{error}</p>}
              
              <button 
                type="submit" 
                className="bg-blue-600 text-white py-3 rounded font-medium mt-4"
                disabled={loading}
              >
                {loading ? 'Logging in...' : 'Login'}
              </button>
            </form>
            
            <p className="text-gray-600 text-center mt-6">
              Don't have an account?{' '}
              <button 
                className="text-blue-600 font-medium"
                onClick={() => setIsLogin(false)}
              >
                Sign up
              </button>
            </p>
            
            <div className="flex items-center my-4">
              <div className="flex-grow border-t border-gray-300"></div>
              <span className="px-4 text-gray-500 text-sm">or</span>
              <div className="flex-grow border-t border-gray-300"></div>
            </div>
          </div>
        ) : (
          // Image section when signup is active
          <div className="p-8 flex flex-col justify-center items-center h-full">
            <img src="/api/placeholder/400/300" alt="Analytics Dashboard" className="mb-8" />
            <h3 className="text-xl font-bold text-gray-800">Track Your Progress</h3>
            <p className="text-gray-600 text-center mt-2 max-w-xs">
              Our intuitive dashboard helps you visualize and analyze your data in real-time.
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default AuthPage;